import React, { useState } from "react";
import { Link } from "react-router-dom";


function Sidebar() {
  const [isSamal, setIsSamal] = useState(localStorage.getItem("isSamal") === "yes");

  const toggleSidebar = () => {
    setIsSamal(!isSamal);
    localStorage.setItem("isSamal", isSamal ? "no" : "yes");
  };

  return (
    <div>
      <div
        id="SidbarId"
        className={`d-flex flex-column flex-shrink-0 p-3 text-bg-dark position-relative ${isSamal ? 'sidebar-samal' : ''}`}
      >
        <span className="close position-absolute">
          <i onClick={toggleSidebar} className="bi bi-x-circle"></i>
        </span>

        <Link
          to="/"
          className="d-flex align-items-center mb-3 mb-md-0 me-md-auto text-white text-decoration-none"
        >
          <i className="bi bi-lightning-charge"></i>
          <span className="fs-4 Sidebar-text">Sidebar</span>
        </Link>

        <hr />

        <ul className="nav nav-pills flex-column mb-auto">
          <li className="nav-item">
            <Link to="/" className="nav-link " aria-current="page">
            <i class="bi bi-house-door"></i>
              <span className="Sidebar-text ms-2">Home</span>
            </Link>
          </li>

          <li>
            <Link to="blog" className="nav-link text-white">
              <i className="bi bi-file-earmark-post"></i>
              <span className="Sidebar-text ms-2">Blog</span>
            </Link>
          </li>
        </ul>
        <hr />
      </div>
    </div>
  );
}

export default Sidebar;
